<template>
  <div class="home">
     <h3></h3>
    <div>
      <input type="file" accept=".png,.jpg" multiple @change="changeMultipleFiles" ref="files"/>
      <span v-if="files.length">{{files.length}}</span>
      <button @click="uploadMultipleFiles"></button>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import axios from 'axios';
export default {
  name: 'Home',
  data() {
    return {
      file: {},
      files: [],
      name: ''
    }
  },
  methods: {
    changeMultipleFiles(e) {
      this.files = [...e.target.files]
      this.$refs.files.value = null;
    },
    uploadMultipleFiles() {
      if (!this.files.length) {
        alert('');
        return;
      }
      const fd = new FormData();
      for(let i=0; i < this.files.length; i++) {
        fd.append('files', this.files[i])
      }
      fd.append('name', '123456')
      axios.post('/repair/file/uploadMutipleFiles', fd, {
        headers: {
          'content-type': 'application/form-data'
        }
      }).then(res => {
        window.console.log(res.data)
      })
    }
  }
}
</script>
