import Vue from 'vue'
import Vuex from 'vuex'
import createVuexAlong from 'vuex-along'
Vue.use(Vuex)
const state={
    userId:'',
    avatar:'',
    followingList:[],
    squareLikeList:[]
}
const mutations={
    storeNameAndAvatar(state,payload){
        state.userId=payload.userId,
        state.avatar=payload.avatar
    },
    storeFollowingList(state,payload){
        state.followingList=payload.followingList
    },
    storeSquareLikeList(state,payload){
        state.squareLikeList=payload.squareLikeList
    },
}
export default new Vuex.Store({
    state,
    mutations,
    plugins: [
        createVuexAlong({
        // 
            name: "hello-vuex-along",
            local: {
            // list: ["state"],
            // //  ma   localStorage
            // isFilter: true,
            },
            session: {
            //  ma  a1  sessionStorage
                list: ["state"],
            },
        }),
    ]
})